import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import Col from './Col';

class Row extends PureComponent {
  colClassName = arr => {
    switch (arr.length) {
      case 2:
        return 'col-sm-6';
      case 3:
        return 'col-sm-4';
      case 4:
        return 'col-sm-3';
      default:
        return 'col-sm-12';
    }
  }
  render() {
    const {data: colsData} = this.props;
    const cols = colsData.reduce((memo, value) => {
      const {Col} = value;
      if (Col && memo.indexOf(Col) === -1) {
        return [...memo, Col];
      }
      return memo;
    }, []).sort();
    const colClassName = this.colClassName(cols);
    return (
      <div className="row">
        {
          cols.map(col => {
            const data = colsData.filter(value => value.Col === col);
            return (
              <Col key={`col_${col}`} colClassName={colClassName} data={data}/>
            );
          })
        }
      </div>
    );
  }
}

Row.defaultProps = {
  data: []
};

Row.propTypes = {
  data: PropTypes.array
};

export default Row;



