import React from 'react';
import PropTypes from 'prop-types';

const InputForm = ({data, onHandleSubmit}) => {
  let formRef;
  const handleFormSubmit = ev => {
    ev.preventDefault();
    onHandleSubmit(formRef.value);
  };
  const setRef = node => {
    formRef = node;
  };
  return (
    <form onSubmit={handleFormSubmit}>
      <div className="form-group">
        <textarea ref={setRef} rows="5" className="form-control" defaultValue={JSON.stringify(data, null, ' ')}/>
      </div>
      <div className="form-group">
        <button className="btn btn-primary" type="submit">Render JSON FormModel</button>
      </div>
    </form>
  );
};

InputForm.propTypes = {
  data: PropTypes.object,
  onHandleSubmit: PropTypes.func.isRequired
};

InputForm.defaultProps = {
  data: {}
};

export default InputForm;



