import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';

class Field extends PureComponent {
  fieldType = () => {
    const {data: {Name, FormFieldID, IsHidden, FieldEditorType, DataType, IsRequired}} = this.props;
    let type = IsHidden ? 'hidden' : 'text';
    type = DataType === 'datetime' ? 'date' : type;

    switch (FieldEditorType) {
      case 20:
        return <textarea className="form-control" name={Name} id={FormFieldID} required={IsRequired}/>;
      case 12:
        return (
          <select className="form-control" name={Name} id={FormFieldID} required={IsRequired}>
            <option>Value1</option>
            <option>Value2</option>
            <option>Value3</option>
          </select>
        );
      default:
        return <input className="form-control" name={Name} id={FormFieldID} type={type} required={IsRequired}/>;
    }
  }
  render() {
    const {data: {DisplayText, IsHidden, IsRequired}, colClassName} = this.props;
    const required = IsRequired ? '*' : '';
    return (
      <div className={`form-group ${colClassName}`}>
        {
          !IsHidden &&
          <label>{DisplayText} {required}</label>
        }
        {
          this.fieldType()
        }
      </div>
    );
  }
}

Field.defaultProps = {
  data: {},
  colClassName: ''
};

Field.propTypes = {
  data: PropTypes.object,
  colClassName: PropTypes.string
};

export default Field;



