import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import SubCol from './SubCol';

class Col extends PureComponent {
  colClassName = arr => {
    switch (arr.length) {
      case 2:
        return 'col-sm-6';
      case 3:
        return 'col-sm-4';
      case 4:
        return 'col-sm-3';
      default:
        return 'col-sm-12';
    }
  }
  render() {
    const {data: fieldsData, colClassName} = this.props;
    const reduced = fieldsData
      .sort((a, b) => a.FieldOrder - b.FieldOrder)
      .reduce((memo, value, index) => {
        const {SubCol} = value;
        const lastIndex = memo.data.length - 1;
        if (index === 0) {
          memo.data[0] = {fields: [value]};
        } else if (memo.lastSubCal && SubCol && memo.lastSubCal === SubCol) {
          memo.data[lastIndex].fields = [...memo.data[lastIndex].fields, value];
        } else {
          memo.data = [...memo.data, {fields: [value]}];
        }
        memo.lastSubCal = SubCol;
        memo.groupName = value.GroupName;
        return memo;
      }, {lastSubCal: null, data: [], groupName: ''});
    const subCols = reduced.data.map((row, ind) => {
      row.colClassName = this.colClassName(row.fields);
      row.key = `subcol_${ind}`;
      return row;
    });
    return (
      <div className={colClassName}>
        <h4>{reduced.groupName}</h4>
        {
          subCols.map(value => <SubCol key={value.key} data={value}/>)
        }
      </div>
    );
  }
}

Col.defaultProps = {
  data: [],
  colClassName: ''
};

Col.propTypes = {
  data: PropTypes.array,
  colClassName: PropTypes.string
};

export default Col;



