import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import Row from './Row';

class Form extends PureComponent {
  render() {
    const {data: {Form}} = this.props;
    if (!Form || !Form.FormFields) {
      return false;
    }
    const {FormID, Title} = Form;
    const rows = Form.FormFields.reduce((memo, value) => {
      const {Row} = value;
      if (Row && memo.indexOf(Row) === -1) {
        return [...memo, Row];
      }
      return memo;
    }, []).sort();
    return (
      <div className="panel panel-default">
        <div className="panel-heading"><h4>{Title}</h4></div>
        <div className="panel-body">
          <form id={FormID} name={FormID}>
            {
              rows.map(row => {
                const data = Form.FormFields.filter(value => value.Row);
                return (
                  <Row key={`row_${row}`} data={data}/>
                );
              })
            }
          </form>
        </div>
      </div>
    );
  }
}

Form.defaultProps = {
  data: {}
};

Form.propTypes = {
  data: PropTypes.object
};

export default Form;



