import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import Field from './Field';

class SubCol extends PureComponent {
  render() {
    const {data: {colClassName, fields}} = this.props;
    return (
      <div>
        {
          fields.map(field => <Field key={`field_${field.FormFieldID}`} colClassName={colClassName} data={field}/>)
        }
      </div>
    );
  }
}

SubCol.defaultProps = {
  data: {}
};

SubCol.propTypes = {
  data: PropTypes.object
};

export default SubCol;



